
#pag-welcome {
  position: relative; 
  display: flex;
  text-align: center;
  align-items: center;                                                /* the 'body' of the page */
  justify-content: center;
  height: 100vh;
  background-color: black;
  background-image: url('/public/ptproiect.jpg'); 
  background-size: cover; 
  background-position: center;
  animation: none; 
}


#pag-welcome::before {
  content: "";
  position: absolute;
  top: 0;                                               
  left: 0;
  width: 100%;                                                            /* the start-up animation */
  height: 100%;
  background-color: black;
  z-index: 0; 
  animation: fadeOut 1.75s ease-in-out forwards;
}


/* the keyframes used for all animations (background + orange container + the 3 images) */

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}


@keyframes efectpr {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.9;
  }
}


@keyframes efectpr1 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.8;
  }
}


.hover-text {
  display: none;
  position: absolute;
  bottom: -40px;                                                      /* The text that appears beneath when hovering */
  left: 50%;
  transform: translateX(-50%);
  padding: 5px 10px;
  background-color: rgb(255, 102, 0);
  color: white;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 18px;
  cursor : not-allowed;
}


/* each of the 3 images is in a icon-container */

.icon-container {
  position: relative;
  display: inline-block;
  margin-right: 35px;
}


.icon-container:last-child {
  margin-right: 0;
}


.icon-container:hover .hover-text {
  display: block;
}
