
#body {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100vh;                                                          
  background-color: black;                                                
  background-image: url('/public/ptproiect.jpg'); 
  background-size: cover; 
  background-position: center;
}


#midcontainer {
  width: 800px;                                                  /* the form's container */         
  text-align: center;
}


#midcontainer input {
  width: 375px;                                                            
  height: 50px;                                                           
  border-radius: 10px;                                          /* the inputs of the form */
  border: none; 
  font-size: 20px;                                                         
  background-color: white;
  background-position: 10px 10px; 
  background-repeat: no-repeat;
  padding: 12px 20px;                                                     
  box-sizing: border-box; 
}


#midcontainer input:focus {
  outline: none; 
  border: none; 
  box-shadow: none; 
}


#midcontainer input:focus::placeholder {
  color: rgba(0, 0, 0, 0.4); 
}


#signupbutton {
  cursor: pointer;
}


#text {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;             /* the ----or---- text */
  font-size: 30px;
  color: white;
}


#a1{
  font-size: 36px !important;
}
